import React, { Component } from 'react';
import Code from '../../comp/Code';

export default class MyFlat extends Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <div className="article-desc">
                <p className="article-title"><span className="textShadow">Flat</span></p>
                <div className="quote">
					1. <br />
					2. <br />
					3. <br />
					4. 
				</div>
                <Code code={`
// es5
Array.prototype.myFlat = function() {
    const _this = this;
    let res = [];
    let cycle = function(arr) {
        arr.forEach(item => {
            if (Array.isArray(item)) {
                cycle(item);
            } else {
                res.push(item);
            }
        })
    }
    cycle(_this);
    return res;
}

// reduce
function myFlat(arr) {
    return arr.reduce((pre, cur) => {
        return pre.concat(Array.isArray(cur) ? myFlat(cur) : cur);
    }, []);
}

// 
function flat (arr, count = 0) {
    var res = [], newcount = 0;
    function flatFn(arr) {
        arr.forEach((item, index) => {
            if (Array.isArray(item)) {
                if (newcount < count) {
                    newcount++;
                    flatFn(item);
                } else {
                    res.push(item);
                }
            } else {
                res.push(item);
                if (index == arr.length - 1) {
                    newcount = 0;
                }
            }
        })
    }
    flatFn(arr);
    return res;
}
`} />
            </div>
        )
    }
}