

import React, { Component } from 'react';
import Code from '../comp/Code';

export default class SelectionSort extends Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <div className="article-desc">
                <p className='article-title'><span className="textShadow">selectionSort</span></p>
                <p className="item-title">1. </p>
                <div className="quote">
                    <a href="https://www.juejin.im/post/6844903496932655112" target="_blank"></a><br />
                    
                </div>

                <p className="item-title">2. </p>
                <Code code={`
function selectionSort(arr) {
    let minIndex;
    for (let i = 0; i < arr.length - 1; i++) {
        minIndex = i;
        for (let j = i + 1; j < arr.length; j++) {
            if (arr[minIndex] > arr[j]) {
                minIndex = j;
            }
        }
        var temp = arr[i];
        arr[i] = arr[minIndex];
        arr[minIndex] = temp;
    }
}`} />
                <p className="item-title">3. </p>
                <div className="quote">
                    O(n^2)<br />
                    O(1);
                </div>
            </div>
        )
    }
}