import React, { Component } from 'react';
import Code from '../comp/Code';

export default class QuickSort extends Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <div className="article-desc">
                <p className='article-title'><span className="textShadow">quickSort</span></p>
                <p className="item-title">1. </p>
                <div className="quote">
                    <a href="https://www.juejin.im/post/6844903496932655112" target="_blank"></a><br />
                    
                </div>

                <p className="item-title">2. </p>
                <Code code={`
function quickSort(arr) {
    var temp = arr[0];
    var left = [];
    var right = [];
    for (let i = 0; i < arr.length; i++) {
        if (arr[i] > temp) {
            right.push(arr[i]);
        } else {
            left.push(arr[i]);
        }
    }
    return quickSort(left).concat([temp], quickSort(right));
}`} />
                <p className="item-title">3. </p>
                <div className="quote">
                    O(nlogn)O(n^2)<br />
                    lognO(logn)
                </div>
            </div>
        )
    }
}