import React, { Component } from 'react';
import Code from '../../comp/Code';

export default class MyLet extends Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <div className="article-desc">
                <p className="article-title"><span className="textShadow">instanceof</span></p>
                <div className="quote">
					1. <br />
					2. <br />
					3. <br />
					4. null
				</div>
				<Code code={`
// obj1  obj2
function myInstanceof(obj1, obj2) {
	let left = obj1.__proto__;
	let right = obj2.prototype;
	while(true) {
		if (left === null) {
			return false;
		}
		if (left !== right) {
			left = left.__proto__;
		} else {
			return true;
		}
	}
}

function myInstanceof(obj1, obj2) {
	let left = obj1.__proto__;
	let right = obj2.prototype;
	if (left === null) {
		return false
	}
	if (left === right ) {
		return true;
	} else {
		// left = left.__proto__;
		myInstanceof(left, obj2);
	}
}
// 
// Object.getPrototypeOf ( )
function myInstanceof(instanObj, classFn) {
	let prototype = classFn.prototype;
	let proto = instanObj.__proto__;
	while(true) {
		if (proto === null) {
			return false;
		}
		if (proto === prototype) {
			return true;
		}
		proto = proto.__proto__;
	}
}
`} />
            </div>
        )
    }
}