import React, { Component } from 'react';
import Code from '../comp/Code';
import bubblesortIcon from '../../img/bubblesort';

export default class BubbleSort extends Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <div className="article-desc">
                <p className='article-title'><span className="textShadow">bubblesort</span></p>
                <p className="item-title">1. </p>
                <div className="quote">
                    <a href="https://www.juejin.im/post/6844903496932655112" target="_blank"></a><br />
                    
                </div>
                <img src={bubblesortIcon} alt='' />

                <p className="item-title">2. </p>
                <Code code={`
function bubbleSort(arr) {
    var isSwap = false;
    for (let i = 0; i < arr.length -1; i++) {
        for (let j = 0; j < arr.length - i - 1; j++) {
            if (arr[j] > arr[j + 1]) {
                isSwap = true;
                var temp = arr[j];
                arr[j] = arr[j+1];
                arr[j+1] = temp;
            }
        }
        if (!isSwap) {
            return; //arr
        }
    }
}`} />
                <p className="item-title">3. </p>
                <div className="quote">
                    O(n^2),O(n);<br />
                    O(1);
                </div>
            </div>
        )
    }
}