				<Code code={`
static all(promises) {
	const values = new Array(promises.length);
	var resolvedCount = 0;
	return new Promise((resolve, reject) => {
		// promisepromise
		promises.forEach((item, index) => {
			// promisepromise
			Promise.resolve(item).then((value) => {
				values[index] = value;
				resolvedCount++;
				
				// 
				if (resolvedCount === promises.length) {
					resolve(values);
				}
			}, reason => {
				// return promiserejected
				reject(reason);
			});
		})
	});
}`} />
