// & 

import React, { Component } from 'react';
import Code from './../../comp/Code';


export default class Currying extends Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <div className="article-desc">
                <p className="article-title">
                    <span className="textShadow"></span>
                </p>

                <h3>1. </h3>
                <div className="quote">
                Currying
                </div>
                <Code code={`
// 
function add(x, y) {
    return x + y;
}
add(1, 2); // 3

// 
function curryingAdd(x) {
    return function(y) {
        return x + y;
    }
}
curryingAdd(1)(2); // 3
`} />
                <h3>2. </h3>
                <div className="quote">
                    1. <br />
                    2. <br />
                    3. bind
                </div>
                <Code code={`
// 
var a = curringAdd(1);
a(2);
a(3);

// 
curringAdd(1)(2);

// bind
Function.prototype.myBind = function(content) {
    const self = this;
    const args = Array.prototype.slice.call(arguments, 1);
    return function() {
        return self.apply(content, args);
    }
}`} />
                <h3>3. </h3>
                <Code code={`
function currying(fn, ...args) {
    return function() {
        var params = args.concat(Array.from(arguments));
        var length = fn.length;
        if (length == params.length) {
            return fn(...params);
        } else {
            return currying(fn, ...params);
        }
    }
}
function test(a,b,c, d) {
    return a+b+c+d;
}
var fn = currying(test);
console.log(fn(1)(2)(3)(4));`} />
            </div>
        )
    }
}